import { useState } from "react";
import { useRouter } from "next/router";

export default function Login() {
  const router = useRouter();

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);

  const handleLogin = (e) => {
    e.preventDefault();

    if (!username || !password) {
      alert("โ๏ธ ุงูุฑุฌุงุก ุฅุฏุฎุงู ุงูุจูุงูุงุช ูุงููุฉ");
      return;
    }

    // ----------------------------
    // 1๏ธโฃ ุชุญุฏูุฏ ุงูุฏูุฑ ุชููุงุฆููุง
    // ----------------------------
    let role = "admin";
    let redirect = "/dashboard";

    const u = username.toLowerCase();

    if (u === "ph") {
      role = "pharmacist";
      redirect = "/pharmacist";
    }

    if (u === "ca") {
      role = "cashier";
      redirect = "/cashier";
    }

    // ----------------------------
    // 2๏ธโฃ ุงูุตูุงุญูุงุช ุญุณุจ ุงูุฏูุฑ
    // ----------------------------
    let permissions = [];

    if (role === "admin") {
      permissions = [
        "manage_users",
        "manage_medicines",
        "add_sale",
        "view_reports",
        "view_inventory",
      ];
    }

    if (role === "pharmacist") {
      permissions = ["manage_medicines", "add_sale", "view_inventory"];
    }

    if (role === "cashier") {
      permissions = ["add_sale"];
    }

    // ----------------------------
    // 3๏ธโฃ ุชุฎุฒูู ุจูุงูุงุช ุงููุณุชุฎุฏู
    // ----------------------------
    const user = {
      username,
      name: username,
      role,
      permissions,
    };

    localStorage.setItem("pharmacy_user", JSON.stringify(user));
    localStorage.setItem("pharmacy_token", "demo-token");

    // ----------------------------
    // 4๏ธโฃ ุฅุถุงูุฉ ุณุฌู ุฏุฎูู โ ุขุฎุฑ 5 ููุท
    // ----------------------------
    try {
      const raw = localStorage.getItem("login_history") || "[]";
      const list = JSON.parse(raw);

      list.unshift({
        username,
        time: new Date().toLocaleString("ar-EG"),
        status: "ูุฌุงุญ",
      });

      localStorage.setItem("login_history", JSON.stringify(list.slice(0, 5)));
    } catch (err) {
      console.error("History error:", err);
    }

    // ----------------------------
    // 5๏ธโฃ ุงูุชูุฌูู
    // ----------------------------
    router.push(redirect);
  };

  return (
    <div
      dir="rtl"
      className="grid min-h-screen px-4 place-items-center bg-gradient-to-br from-sky-100 to-white"
    >
      <div className="w-full max-w-md p-8 bg-white shadow-lg rounded-2xl">
        {/* ุงูุดุนุงุฑ */}
        <div className="flex flex-col items-center mb-6 text-center">
          <div className="flex items-center justify-center w-16 h-16 mb-3 text-3xl text-white rounded-full shadow-md bg-sky-500">
            ๐
          </div>
          <h1 className="text-2xl font-bold text-gray-800">ุตูุฏููุฉ ุงููุนููู</h1>
          <p className="mt-1 text-sm text-gray-500">
            ูุฑุฌู ุชุณุฌูู ุงูุฏุฎูู ูููุชุงุจุนุฉ
          </p>
        </div>

        {/* ุงููููุฐุฌ */}
        <form onSubmit={handleLogin} className="space-y-5 text-right">
          {/* ุงุณู ุงููุณุชุฎุฏู */}
          <div className="relative">
            <input
              type="text"
              placeholder="ุงุณู ุงููุณุชุฎุฏู"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="w-full px-10 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-sky-400 focus:border-sky-400"
            />
            <span className="absolute inset-y-0 flex items-center text-lg text-gray-500 right-3">
              ๐ค
            </span>
          </div>

          {/* ูููุฉ ุงููุฑูุฑ */}
          <div className="relative">
            <input
              type={showPassword ? "text" : "password"}
              placeholder="ูููุฉ ุงููุฑูุฑ"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full px-10 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-sky-400 focus:border-sky-400"
            />
            <span className="absolute inset-y-0 flex items-center text-lg text-gray-500 right-3">
              ๐
            </span>

            <button
              type="button"
              onClick={() => setShowPassword((v) => !v)}
              className="absolute inset-y-0 flex items-center text-gray-500 left-3 hover:text-gray-700"
            >
              {showPassword ? "๐" : "๐๏ธ"}
            </button>
          </div>

          {/* ุฒุฑ ุงูุฏุฎูู */}
          <button
            type="submit"
            className="w-full py-2.5 text-white bg-sky-600 hover:bg-sky-700 rounded-md shadow-md transition"
          >
            ุชุณุฌูู ุงูุฏุฎูู
          </button>
        </form>

        <p className="mt-6 text-xs text-center text-gray-400">
          ยฉ 2025 ุฌููุน ุงูุญููู ูุญููุธุฉ โ ูุธุงู ุฅุฏุงุฑุฉ ุงูุตูุฏููุฉ
        </p>
      </div>
    </div>
  );
}










